.wrapper {
    width: 100vw;
    padding: 20px;
    margin-top: 20px;
    background: #fff;
}

@media (min-width: 750px) {
    .wrapper {
        width: 750px;
    }
}

.wrapper h1,
.wrapper h3 {
    margin: 0;
}

.wrapper h3 {
    font-weight: bold;
}

.spacer {
    width: 30px;
}
